"use client";

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Alert, AlertDescription } from '@/components/ui/alert';

export default function HomePage() {
  const [isLoading, setIsLoading] = useState(false);
  const [shareLink, setShareLink] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  const handleShareLocation = async () => {
    setIsLoading(true);
    setError('');
    setSuccess('');
    setShareLink('');

    try {
      // Check if geolocation is supported
      if (!navigator.geolocation) {
        throw new Error('Geolocation is not supported by this browser');
      }

      // Get current position
      const position = await new Promise<GeolocationPosition>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 0
        });
      });

      const { latitude, longitude } = position.coords;

      // Send location to API
      const response = await fetch('/api/create-share', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          lat: latitude,
          lng: longitude
        })
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to create share link');
      }

      // Generate share link
      const link = `${window.location.origin}/track/${data.token}`;
      setShareLink(link);
      setSuccess('Location share link created successfully!');

    } catch (err: any) {
      if (err.code === 1) {
        setError('Location access denied. Please enable location permissions and try again.');
      } else if (err.code === 2) {
        setError('Location unavailable. Please check your GPS settings.');
      } else if (err.code === 3) {
        setError('Location request timed out. Please try again.');
      } else {
        setError(err.message || 'Failed to share location. Please try again.');
      }
    } finally {
      setIsLoading(false);
    }
  };

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(shareLink);
      setSuccess('Link copied to clipboard!');
    } catch (err) {
      setError('Failed to copy link. Please copy manually.');
    }
  };

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <div className="w-full max-w-md space-y-6">
        <div className="text-center space-y-2">
          <h1 className="text-3xl font-bold tracking-tight">Location Sharing</h1>
          <p className="text-muted-foreground">
            Share your current location with others safely and ethically
          </p>
        </div>

        <Card>
          <CardHeader>
            <CardTitle>Share Your Location</CardTitle>
            <CardDescription>
              Your location will only be shared after you explicitly grant permission. 
              You have full control over when and with whom you share your location.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <h3 className="font-medium">How it works:</h3>
              <ul className="text-sm text-muted-foreground space-y-1">
                <li>• Click the button below to enable location sharing</li>
                <li>• Grant location permission when prompted</li>
                <li>• Receive a unique link to share with others</li>
                <li>• Others can view your location using the link</li>
              </ul>
            </div>

            <Button 
              onClick={handleShareLocation} 
              disabled={isLoading}
              className="w-full"
            >
              {isLoading ? 'Getting Location...' : 'Enable Location Sharing'}
            </Button>

            {error && (
              <Alert variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            {success && (
              <Alert>
                <AlertDescription>{success}</AlertDescription>
              </Alert>
            )}

            {shareLink && (
              <div className="space-y-2">
                <label className="text-sm font-medium">Share this link:</label>
                <div className="flex space-x-2">
                  <Input 
                    value={shareLink} 
                    readOnly 
                    className="flex-1"
                  />
                  <Button 
                    onClick={copyToClipboard}
                    variant="outline"
                  >
                    Copy
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>

        <div className="text-center">
          <p className="text-xs text-muted-foreground">
            Your privacy is important. Location data is only shared when you explicitly consent.
          </p>
        </div>
      </div>
    </div>
  );
}
